@import './../mixins/_Mixins';

.img-box-road {
    height: 749px; }


.road-img {
    object-fit: none;
    max-width: 100%;
    height: 100%;

    position: relative;
    margin-top: 40px; }

.news-slogan {
    position: absolute;
    font-weight: 700;
    font-size: 40px;
    line-height: 54px;
    top: 750px;
    right: 970px;
