@import './../mixins/_Mixins';
.license-title {
    font-weight: 700;
    font-size: 48px;
    line-height: 65px;
    color: #282828;
    text-align: center;
    margin-top: 70px; }
.license-group {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    margin-top: 70px;
    margin-bottom: 70px;
    @include media_max(1480px) {
        grid-template-columns: 1fr 1fr;
        padding: 0px 100px;
        width: 100%;
        text-align: center;
        @include media_max(1153px) {
            grid-template-columns: 1fr;
            padding: 0px 15px;
            // +media_max(550px)
            //         </div>
 } } }            //         </div>
.license-img {
    width: 462px;
    height: 639px;
    @include media_max(550px) {
        width: 390px;
        height: 539px; } }


