@import './../mixins/_Mixins';



.purchase-title {
    font-weight: 700;
    font-size: 48px;
    color: #282828;
    line-height: 65px;
    text-align: center;
    @include media_max(768px) {
        font-size: 24px; } }
.purchase-slogan {
    margin-left: 40px;
    font-weight: 700;
    font-size: 24px;
    color: #282828;
    line-height: 32px;
    margin-top: 70px;
    @include media_max(768px) {
        font-size: 16px;
        line-height: 19px;
        margin-top: 30px; } }
.purchase-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 0px 20px 0px; }
.chase__item {
    grid-column-gap: 20px; }
.purchase-group {
    background: #F4F8FC; }
.purchase__item {
    background: #B3CFEB;
    // z-index: 1
 }    // padding: 0px 20px 0px
.slogan {
    display: inline-block;
    padding: 12px 26px;
    font-weight: 600;
    font-size: 18px;
    color: #282828;
    line-height: 24px;
    @include media_max(768px) {
        font-size: 14px;
        line-height: 16px;
        padding: 12px 16px; } }
.purchase-box {
    display: flex;
    margin-bottom: 120px;
    @include media_max(1150px) {
        flex-wrap: wrap;
        justify-content: center;
        width: 100%;
        padding: 0px 20px;
        margin-bottom: 60px;
        .purchase-left {
            margin-bottom: 50px;
            width: 100%;
            min-height: auto; }
        .purchase-right {
            width: 100%;
            min-height: auto;
            margin-left: 0px; } } }



.slogan-decor {
    display: inline-block;
    padding: 12px 300px;
    font-size: 18px;
    color: #282828;
    line-height: 24px; }
.slogan-chase {
    display: inline-block;
    padding: 15px 26px;
    font-weight: 600;
    font-size: 18px;
    color: #282828;
    line-height: 24px; }
.purchase-right {
    margin-left: 30px;
    justify-content: center;
    align-items: center; }
.purchase-left {
    justify-content: center;
    align-items: center; }

.slogan-left {
    padding: 15px 26px;
    width: 100%;
    margin: auto;
    text-align: center;
    display: inline-block;
    font-weight: 600;
    font-size: 18px;
    color: #282828;
    line-height: 24px;
    background: #B3CFEB;
    line-height: 24px;
    @include media_max(767px) {
        font-size: 16px; } }
.left-group {
    background: #F4F8FC;
 }    // height: 342px
.left-slogan {
    font-weight: 600;
    font-size: 18px;
    line-height: 24px;
    display: inline-block;
    padding: 15px 13px  20px;
    margin-bottom: 5px;
    margin-left: 15px;
    @include media_max(767px) {
        font-size: 14px;
        line-height: 16px; } }
.right-group {
    background: #F4F8FC; }

.slogan-right {
    padding: 15px 26px;
    display: inline-block;
    font-size: 18px;
    color: #282828;
    line-height: 24px;
    background: #B3CFEB;
    font-weight: 600;
    width: 100%;
    margin: auto;
    text-align: center;
    @include media_max(767px) {
        font-size: 16px; } }
.right-slogan {
    font-weight: 600;
    font-size: 18px;
    display: inline-block;
    padding: 15px 13px  20px;
    margin-bottom: 0px;
    margin-left: 15px;
    @include media_max(767px) {
        font-size: 14px;
        line-height: 16px; } }

.copy {
    position: absolute;
    display: flex;
    align-items: center;
    left: 1360px;
    top: 1780px; }
.purchase__link {
    font-weight: 700;
    font-size: 18px;
    color: #357ECB; }
.arrow-img {
    margin-left: 10px; }
