@import './../mixins/_Mixins';

.map-banner {
    height: 600px; }
.contact-img {
    object-fit: none;
    max-width: 100%;
    height: 100%;
    position: relative; }
.contact-title-decor {
    font-weight: 700;
    font-size: 48px;
    color: #282828;
    margin-bottom: 60px;
    @include media_max(1365px) {
        font-size: 38px;
        margin-bottom: 30px; }
    @include media_max(939px) {
        font-size: 32px; }
    @include media_max(767px) {
        font-size: 24px; } }
.contact-info {
    padding: 0px 30px;
    @include media_max(1365px) {
        padding: 0px 10px; } }
.info-text {
    @include media_max(1365px) {
        margin-bottom: 0px; } }
.select {
    font-weight: 700;
    font-size: 36px;
    color: #282828;
    line-height: 49px;
    @include media_max(1365px) {
        font-size: 30px; }
    @include media_max(905px) {
        font-size: 26px; }
    @include media-max(840px) {
        font-size: 18px; } }

.itext {
    font-weight: 600;
    font-size: 36px;
    color: #282828;
    line-height: 49px;
    margin-left: 8px;
    @include media_max(1365px) {
        font-size: 28px;
        line-height: 29px;
        margin-bottom: 0px; }
    @include media_max(1023px) {
        font-size: 24px;
        line-height: 16px; }
    @include media_max(875px) {
        font-size: 16px;
        margin-top: 0px; } }

.contact-img {
    margin-bottom: 80px;
    margin-top: 50px; }

.contact-title-decor {
    font-weight: 700;
    font-size: 48px;
    color: #282828;
    margin-bottom: 0;
    text-align: center;
    margin-bottom: 80px;
    @include media_max(1365px) {
        font-size: 38px; }
    @include media_max(939px) {
        font-size: 32px; }
    @include media_max(767px) {
        font-size: 24px;
        margin-bottom: 20px; } }

.form-slogan {
    font-weight: 700;
    font-size: 22px;
    color: #282828;
    line-height: 30px;
    margin-top: 70px; }
.form-input {
    max-width: 100%;
    margin: auto;
    font-weight: 400;
    font-size: 18px;
    color: #A0A0A0;
    padding: 12px 185px 11px 20px;
    border: 2px solid #357ECB;
    border-radius: 4px; }


.form-group {
    padding: 0px 100px;
    margin-top: 80px;
    @include media_max(1365px) {
        padding: 0px 80px; }
    @include media_max(939px) {
        padding: 0px 40px;
        margin-bottom: 60px; }
    @include media_max(767px) {
        padding: 0px 30px;
        margin-top: 40px; } }
.form-contact {
    border: 4px solid #357ECB;
    padding: 50px 300px;
    @include media_max(1365px) {
        padding: 0px 150px; }
    @include media_max(939px) {
        padding: 0px 80px; }
    @include media_max(767px) {
        padding: 0px 20px; } }

.input .ainput {
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    resize: vertical; }

.input-title {
    font-weight: 700;
    font-size: 22px;
    color: #282828;
    line-height: 30px;
    @include media_max(767px) {
        font-size: 16px; } }
.finput {
    min-width: 100%;
    height: 48px;
    // padding: 0 40px 0 12px
    background-color: #fff;
    border-radius: 4px;
    border: 2px solid #357ECB;
    font-size: 12px;
    line-height: 20px;
    color: #7F7F7F;
    margin-top: 15px; }
.ainput {
    min-width: 100%;
    height: 265px;
    // padding: 15px 40px 150px 12px
    background-color: #fff;
    border-radius: 4px;
    border: 2px solid #357ECB;
    font-weight: normal;
    font-size: 12px;
    line-height: 20px;
    color: #7F7F7F;
    margin-top: 15px;
    @include media_max(767px) {
        height: 120px; } }
.input-box {
    margin-bottom: 100px;
    @include media_max(1365px) {
        margin-bottom: 60px; }
    @include media_max(939px) {
        margin-bottom: 40px; }
    @include media_max(768px) {
        margin-bottom: 10px; } }


.form-input-decor {
    // max-width: 100%
    // margin: auto
    font-weight: 400;
    font-size: 18px;
    color: #A0A0A0;
    padding: 12px 694px 11px 20px;
    border: 2px solid #357ECB;
    border-radius: 4px; }
.form-message {
    // max-width: 100%
    // margin: auto
    border: 2px solid #357ECB;
    border-radius: 4px;
    font-size: 18px;
    color: #A0A0A0;
    font-weight: 400;
    padding: 20px 640px 80px 20px;
    margin-bottom: 80px; }
.btn {
    text-align: center; }
.form-btn {
    display: inline-block;
    padding: 13px 62px;
    background: #357ECB;
    color: #FFFFFF;
    font-weight: 700;
    font-size: 22px;
    line-height: 30px;
    margin-top: 70px;
    cursor: pointer; }
